import React, { useState } from "react";
import { Offcanvas, Button } from "react-bootstrap";
import "./TagsListMobile.css";
import TagsList from "../TagsList/TagsList";
export default function TagsListMobile(props) {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <div>
      <div className="filter-tags-button-container" onClick={handleShow}>
        <img
          alt="filter events"
          className="filter-tags-button-img"
          src="https://www.codingninjas.com/assets-landing/images/filter-events-wave.svg"
        />
        <div className="filter-tags-button-text"> Filter Events </div>
      </div>
      <Offcanvas
        style={{ height: "100vh" }}
        placement="bottom"
        show={show}
        onHide={handleClose}
        {...props}
      >
        <Offcanvas.Header closeButton>
          <Offcanvas.Title>Offcanvas</Offcanvas.Title>
        </Offcanvas.Header>
        <Offcanvas.Body style={{ paddingBottom: "50px" }}>
          <TagsList
            tags={props.tags}
            handleSelectedTags={props.handleSelectedTags}
            mobileView={true}
          />
        </Offcanvas.Body>
      </Offcanvas>
    </div>
  );
}
